body {
  font-family: SF Pro Text;
}
P,
h1 {
  margin: 0;
}

.header {
  max-width:1200px;
  margin:0 auto;
  padding-top: 40px;
  padding-bottom: 56px;
  .author {
    padding-bottom: 24px;
    border-bottom: 2px solid black;
    font-size: 80px;
    font-weight: 400;
  }
  .photo {
    padding-top: 48px;
    display: block;
    width: 324px;
  }
}
.profile {
  max-width:1200px;
  margin:0 auto;
  padding-bottom: 80px;
  .profile-list {
    list-style: none;
    &__item {
      font-size: 24px;
      font-weight: 400;
      line-height: 36px;
    }
  }
}
.education {
  max-width: 1200px;
  margin:0 auto;
  padding-bottom: 72px;
  .education-list {
    list-style-type: none;
    &__item {
      font-size: 24px;
      font-weight: 400;
      padding-bottom: 24px;
      &:last-child {
        padding-bottom: 0px;
      }
    }
  }
}
.skill {
  background-color: #415165;
  padding: 24px 102px 52px;
  .skill-title {
    width:1200px;
    color: white;
    font-weight: 500;
    text-align: center;
    padding-bottom: 16px;
    margin:0 auto;
    margin-bottom: 40px;
    border-bottom: 2px solid white;
    &__text {
      font-size: 32px;
      color: white;
      margin-right: 16px;
    }
    &__txt {
      font-size: 24px;
      color: white;
    }
  }
  .skill-list {
    width: fit-content;
    margin: 0 auto;
    list-style: none;
    padding-left: 24px;
    border-left: 1px solid #96a9c4;
    &__item {
      font-size: 24px;
      font-weight: 300;
      color: white;
      padding-bottom: 20px;
      &:last-child {
        padding-bottom: 0px;
      }
    }
  }
}
.experience {
  max-width:1200px;
  margin:0 auto;
  padding-top: 72px;
  padding-bottom: 76px;
  .experience-list {
    list-style: none;
    padding-left: 24px;
    margin-left: 24px;
    border-left: 1px solid #96a9c4;
    &__item {
      padding-bottom: 32px;
      &:last-child {
        padding-bottom: 0px;
      }
      .item-title{
        font-size: 24px;
        font-weight: 400;
      }
      .item-img {
        display: block;
        margin: 8px 0px;
        &.-icon2 {
          width: 72px;
        }
      }
      .item-txt {
        width: 60%;
        font-size: 20px;
        font-weight: 300;
        line-height: 30px;
      }
    }
  }
}
.title {
  font-weight: 500;
  padding-bottom: 16px;
  margin-bottom: 16px;
  border-bottom: 2px solid black;
  &__text {
    font-size: 32px;
    margin-right: 16px;
  }
  &__txt {
    font-size: 24px;
    color: #a00404;
  }
}
.footer {
  background-color: #415165;
  .footer-content {
    width: 1200px;
    margin: auto;
    padding: 32px 0px 48px 0px;
    .footer-title {
      width: 100%;
      text-align: center;
      padding-bottom: 8px;
      margin: auto;
      border-bottom: 1px solid white;
      &__text {
        font-size: 32px;
        color: white;
        margin-right: 16px;
      }
      &__txt {
        font-size: 24px;
        color: white;
      }
    }
    .footer-list {
      width: 100%;
      padding: 32px 0px 20px 0px;
      text-align: center;
      &__item {
        font-size: 24px;
        font-weight: 300;
        color: white;
        padding-bottom: 16px;
        &:last-child {
          padding-bottom: 0px;
        }
        .item-link{
          font-size: 24px;
        font-weight: 300;
        color: white;
        text-decoration: none;
        }
      }
    }
    .footer-icon {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      text-align: center;
      &__link {
        display: block;
        width: 48px;
        height: 48px;
        margin-right: 16px;
        overflow: hidden;
        .link-img {
          width: 100%;
        }
        &::last-child {
          margin-right: 0px;
        }
      }
      // &__img {
      //   width: 48px;
      //   height: 48px;
      //   margin-right: 16px;
      //   &:last-child {
      //     margin-right: 0px;
      //   }
      // }
    }
  }
}
